import { Button } from 'react-bootstrap'
import Ospf from './Ospf'
import './Basic.css'
import { ToastContainer, toast } from 'react-toastify'
import 'react-toastify/dist/ReactToastify.css'

const Basic = ({ list, ID, hostname, banner, ospfID }) => {

  const getIPMask = () => {
    let rows = list.split('\n')
    let columns = rows.map((row) => row.split(/[ \t]/))
    return columns
  }
  

  const getInterfacesConfig = ( copy = true) => {
    const addresses = getIPMask()
    let data = ''

    addresses.map(
      (item) => {
        if (item[0]){
          (data += `interface ${item[0]}\nip address ${item[1]} ${item[2]}\nno shutdown\nexit\n`)
        }
      }
    )

    if (copy){
      toast.success('‚öôÔ∏è Interfaces Copiado!');
      navigator.clipboard.writeText(data)
    }
    return data;
  }

  const getBasicConfig = (copy = true) => {
    let basicConfigs = `configure terminal\nhostname ${hostname}_R${ID}\nbanner motd ~${banner}~\nusername cisco password cisco\nservice password-encryption\nno ip domain-lookup\nip domain name cisco.com\ncrypto key generate rsa\n1024\nline vty 0 4\ntransport input ssh\nlogin local\nexit\n`
    if (copy) {
      toast.success('‚å®Ô∏è  Copiado!')
      navigator.clipboard.writeText(basicConfigs);
    };
    return basicConfigs;
  }


  return (
    <div className='basic'>
       <p className="router-id"> ü§ñR{ID}</p>
      <Button variant='outline-primary' onClick={getBasicConfig}>
        Conf. B√°sicas
      </Button>
     
      <Button variant='outline-success' onClick={getInterfacesConfig}>
        Interfaces
      </Button>
      <Ospf getIPMask={getIPMask} ospfID={ospfID} getBasicConfig={getBasicConfig} getInterfacesConfig={getInterfacesConfig}/>

      <ToastContainer
        position='top-right'
        theme='light'
        autoClose={2500}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
      />{' '}
    </div>
  )
}

export default Basic
