import { useRef, useState } from "react";
import Routers from "./components/Routers";
import "./App.css";
import { Button } from "react-bootstrap";

function App() {
  const textRef = useRef();
  const [content, setContent] = useState("");
  const [showExample, setShowExample] = useState(true);
  const [showExampleGroup, setShowExampleGroup] = useState(true);

  const example = `G0/0\t172.16.65.1\t255.255.255.240\nG1/0\t172.16.65.17\t255.255.255.240\nG2/0\t172.16.65.34\t255.255.255.240\nG3/0\t172.16.65.98\t255.255.255.240`;
  const exampleGroup = `G0/0\t172.16.65.1\t255.255.255.240\nG1/0\t172.16.65.17\t255.255.255.240\n\nG2/0\t172.16.65.34\t255.255.255.240\nG3/0\t172.16.65.98\t255.255.255.240`;
  const getRouters = () => {
    let routers = content.split(/\n\s*\n/);
    return routers;
  };

  return (
    <div style={{ margin: "2%" }}>
      <textarea
        className="textarea"
        placeholder="Pegar tabla de direccionamiento formato 'G0/0 172.16.70.1 255.255.255.240'"
        ref={textRef}
        onChange={(e) => setContent(e.target.value)}
      />

      {showExample && (
        <Button
          className="mr-2 mt-3"
          variant="primary"
          onClick={() => {
            textRef.current.value = example;
            setContent(example);
            setShowExample(!showExample);
          }}
        >
          Ejemplo Router
        </Button>
      )}

      {showExampleGroup && showExample && (
        <Button
          className="ml-2 mt-3"
          variant="primary-success"
          onClick={() => {
            textRef.current.value = exampleGroup;
            setContent(exampleGroup);
            setShowExampleGroup(!showExampleGroup);
            setShowExample(!showExample);
          }}
        >
          Ejemplo Varios Routers
        </Button>
      )}

      {!showExample && <Routers RoutersList={getRouters()} />}

    </div>
  );
}

export default App;
